import instance from './http.config'
// 我的账户信息
export const getAccountList = (param) => instance.get(`/admin/bank_card/balance?company_id=${param}`)
// 添加提现账户
export const addWithdrawAccount = (param) => instance.post('/admin/bank_card', param)
// 银行卡列表
export const bankCardList = (param) => instance.get(`/admin/bank_card?company_id=${param}`)
// 删除银行卡
export const deleteBankCard = (param) => instance.delete(`/admin/bank_card?company_id=${param.company_id}&bank_card_id=${param.bank_card_id}`)
// 开户银行卡列表
export const bankList = () => instance.get(`/admin/bank_card/bank`)
// 获得省
export const provinceList = () => instance.get(`/admin/service/getProvinceRegionList?pid=-1`)
// 提现
export const withDraw = (param) => instance.post(`/admin/bank_card/withdraw`, param)



