/** @author 薛鑫欣
 * @description 账户信息
*/
<template>
   <div>
     <div style="width:100%;height:230px">
        <div class="div_left" >
          <p class="balance">账户余额&nbsp;&nbsp;&nbsp;<span style="font-size:25px">{{balance}}</span>&nbsp;元</p>
          <button class="withdraw" @click="withdraw">提现</button>
        </div>
        <div class="div_right">
          <div class="div_service">
            <span >其中服务费收益&nbsp;{{service_charge}}&nbsp;元</span>
          </div>
          <div class="div_distribution">
              <span >其中分销收益&nbsp;{{sales_earnings}}&nbsp;元</span>
          </div>
        </div>
      </div>
      <div  class="div_account">
        <div style="border-bottom: 1px solid rgb(224, 219, 219);width:100%;text-align:left;padding-bottom:5px;height:30px">
          <span style="font-size:20px;font-weight: 500">余额记录</span>
        </div>
        <div style="width:100%;height:100%;margin-top:20px">
        <el-table
          :data="tableData"
          height="500"
          border
          >
          <el-table-column
            prop="create_time"
            label="时间"
            align="center"
            width="250">
          </el-table-column>
          <el-table-column
            prop="bill_tag"
            width="250"
           align="center"
            label="类型">
          </el-table-column>
          <el-table-column
            prop="price"
            label="金额"
            align="center"
            width="250">
          </el-table-column>
          <el-table-column
            prop="order_code"
            align="center"
            label="订单号">
          </el-table-column>
        </el-table>
        </div>
      </div>
   </div>
</template>

<script>
import {getAccountList} from '../../../service/AccountService'
import { getLoginInfo } from '../../../assets/common/utils.js'
export default {
  created () {
    // 面包屑
    this.$store.dispatch('commitChangeTopTitles', ['账户管理', '账户信息'])
  },
  data () {
    return {
      company_id: '',
      balance: '',
      service_charge: '',
      sales_earnings: '',
      tableData: []
    }
  },
  methods: {
    // 进入提现页面
    withdraw () {
      localStorage.setItem('balance', this.balance)
      this.$router.push('/withdraw')
    },
    setType (type) {
      alert(type)
      if (type === 'service') {
        this.type = '阿姨'
      }
    }
  },
  mounted () {
    this.company_id = getLoginInfo('company_id')
    // 展示账户信息
    if (this.company_id) {
      getAccountList(this.company_id).then((res) => {
        this.balance = res.data.balance.balance
        this.service_charge = res.data.balance.service_charge
        this.sales_earnings = res.data.balance.sales_earnings
        if (res.data.lists.length > 0) {
          this.tableData = res.data.lists
          this.tableData.forEach(item => {
            // 类型
            if (item.bill_tag === 'h-in-salary') {
              item.bill_tag = '服务费'
            } else if (item.bill_tag === 'o-in-salary') {
              item.bill_tag = '订单分销费'
            } else if (item.bill_tag === 'c-in-salary') {
              item.bill_tag = '课程分消费'
            } else {
              item.bill_tag = '提现'
            }
            // 支出收入
            if (item.bill_type === 'receipt') { // 收入
              item.price = '+' + item.price
            } else { // 支出
              item.price = '-' + item.price
            }
          })
        }
      })
    }
  }
}
</script>

<style scoped>
.el-table >>> th {
    background-color: #e3eefa;
    border-left: #9FAFCC solid 1px;
    font-family: "\82F9\65B9-\7B80 \5E38\89C4\4F53", "\82F9\65B9-\7B80 \5E38\89C4\4F53", "\82F9\65B9-\7B80 \5E38\89C4\4F53";
    color: #000000;
    font-size: 16px;
    font-weight: bold;
     border:none;
}
.div_left{
  width: 400px;
  height: 200px;
  float: left;;
  margin-top: 10px;
  background: rgb(255, 253, 253);
  border: 1px solid rgb(236, 227, 227);
}
.div_right{
  width: 400px;
  float: left;
  margin-left: 20px;
  height: 200px;
  margin-top: 10px;
}
.div_service {
  width: 350px;
  line-height: 90px;
  height: 90px;
  background: rgb(255, 253, 253);
  border: 1px solid rgb(236, 227, 227);
}
.div_distribution{
  margin-top: 20px;
  width: 350px;
  line-height: 90px;
  height: 90px;
  background: rgb(255, 253, 253);
  border: 1px solid rgb(236, 227, 227);
}
.balance {
  font-weight: 500;
  font-size: 20px;
  margin-top: 70px;
}
.withdraw {
  height: 25px;
  width: 100px;
  border-radius: 5px;
  background: #fff;
}
.div_account{
  width: 100%;
}

</style>



